import * as React from 'react';
import { styled } from '@mui/material/styles';
import Card from '@mui/material/Card';
import CardHeader from '@mui/material/CardHeader';
import CardMedia from '@mui/material/CardMedia';
import CardContent from '@mui/material/CardContent';
import CardActions from '@mui/material/CardActions';
import Collapse from '@mui/material/Collapse';
import Avatar from '@mui/material/Avatar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import { red } from '@mui/material/colors';
import FavoriteIcon from '@mui/icons-material/Favorite';
import ShareIcon from '@mui/icons-material/Share';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import ReactHtmlParser, { processNodes, convertNodeToElement, htmlparser2 } from 'react-html-parser';
import Parse from '../vendor/Parse'
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button'
const { useState } = React;
const VisuallyHiddenInput = styled('input')({
    clip: 'rect(0 0 0 0)',
    clipPath: 'inset(50%)',
    height: 1,
    overflow: 'hidden',
    position: 'absolute',
    bottom: 0,
    left: 0,
    whiteSpace: 'nowrap',
    width: 1,
});

const ExpandMore = styled((props) => {
    const { expand, ...other } = props;
    return <IconButton {...other} />;
})(({ theme, expand }) => ({
    transform: !expand ? 'rotate(0deg)' : 'rotate(180deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
        duration: theme.transitions.duration.shortest,
    }),
}));

export default function RecipeReviewCard({ list = [], editMode, handleDelete, onPost }) {


    const [expanded, setExpanded] = React.useState(false);
    const [newPostPayload, setNewPostPayload] = useState({})


    const handleExpandClick = () => {
        setExpanded(!expanded);
    };



    return (
        <>


            {
                list?.map((post) => <Card>

                    <CardHeader
                        avatar={
                            <Avatar sx={{ bgcolor: red[500] }} aria-label="recipe">
                                A
                            </Avatar>
                        }
                        action={
                            editMode && <IconButton aria-label="settings" onClick={() => handleDelete(post)}>
                                <MoreVertIcon />
                            </IconButton>
                        }
                        title={post?.data?.title}
                        subheader={post.createdAt.toString()}
                    />

                    {
                        post?.data?.image && <CardMedia
                            component="img"
                            height="294"
                            image={post?.data?.image}
                            alt="Paella dish"
                        />
                    }

                    {
                        post.data?.video && <div class="youtube-video-container">
                            <iframe width="560" height="315" src={post.data?.video} title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" referrerpolicy="strict-origin-when-cross-origin" allowfullscreen></iframe>
                        </div>
                    }
                    <CardContent>
                        <Typography variant="body2" color="text.secondary">

                            {ReactHtmlParser(post?.data?.content)}
                        </Typography>
                    </CardContent>

                    <Collapse in={expanded} timeout="auto" unmountOnExit>
                        <CardContent>
                            <Typography paragraph>Method:</Typography>
                            <Typography paragraph>
                                Heat 1/2 cup of the broth in a pot until simmering, add saffron and set
                                aside for 10 minutes.
                            </Typography>
                            <Typography paragraph>
                                Heat oil in a (14- to 16-inch) paella pan or a large, deep skillet over
                                medium-high heat. Add chicken, shrimp and chorizo, and cook, stirring
                                occasionally until lightly browned, 6 to 8 minutes. Transfer shrimp to a
                                large plate and set aside, leaving chicken and chorizo in the pan. Add
                                pimentn, bay leaves, garlic, tomatoes, onion, salt and pepper, and cook,
                                stirring often until thickened and fragrant, about 10 minutes. Add
                                saffron broth and remaining 4 1/2 cups chicken broth; bring to a boil.
                            </Typography>
                            <Typography paragraph>
                                Add rice and stir very gently to distribute. Top with artichokes and
                                peppers, and cook without stirring, until most of the liquid is absorbed,
                                15 to 18 minutes. Reduce heat to medium-low, add reserved shrimp and
                                mussels, tucking them down into the rice, and cook again without
                                stirring, until mussels have opened and rice is just tender, 5 to 7
                                minutes more. (Discard any mussels that don&apos;t open.)
                            </Typography>
                            <Typography>
                                Set aside off of the heat to let rest for 10 minutes, and then serve.
                            </Typography>
                        </CardContent>
                    </Collapse>
                </Card>)
            }
            {
                editMode && <Card>
                    <CardHeader
                        avatar={
                            <Avatar sx={{ bgcolor: red[500] }} aria-label="recipe">
                                A
                            </Avatar>
                        }
                        action={
                            editMode && <IconButton aria-label="settings" onClick={() => handleDelete(post)}>
                                <MoreVertIcon />
                            </IconButton>
                        }
                        title={newPostPayload.title}
                        subheader={""}
                    />
                    {
                        newPostPayload?.image && <CardMedia
                            component="img"
                            height="294"
                            image={newPostPayload.image}
                            alt=""
                        />
                    }

                    {
                        newPostPayload?.video && <div class="youtube-video-container">
                            <iframe width="560" height="315" src={newPostPayload?.video} title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" referrerpolicy="strict-origin-when-cross-origin" allowfullscreen></iframe>
                        </div>
                    }

                    <CardContent>

                        <Typography variant="body2" color="text.secondary">
                            <Box>
                                <TextField fullWidth onChange={(e) => {
                                    setNewPostPayload({
                                        ...newPostPayload,
                                        title: e.target.value,
                                    })
                                }} />
                                <TextField fullWidth onChange={(e) => {
                                    setNewPostPayload({
                                        ...newPostPayload,
                                        content: e.target.value
                                    })
                                }} />
                                <TextField fullWidth placeholder="Video Url" onChange={(e) => {
                                    setNewPostPayload({
                                        ...newPostPayload,
                                        video: e.target.value
                                    })

                                }} />
                            </Box>

                        </Typography>
                    </CardContent>
                    <CardActions disableSpacing>
                        <Button
                            component="label"
                            role={undefined}
                            variant="contained"
                            tabIndex={-1}

                        >
                            Upload file
                            <VisuallyHiddenInput type="file" onChange={(event) => {

                                const file = event.target.files[0];
                                const reader = new FileReader();
                                reader.onloadend = () => {
                                    setNewPostPayload({
                                        ...newPostPayload,
                                        image: reader.result
                                    })
                                };
                                reader.readAsDataURL(file);
                            }} />
                        </Button>
                        <ExpandMore
                            expand={expanded}
                            onClick={handleExpandClick}
                            aria-expanded={expanded}
                            aria-label="show more"

                        >
                            <ExpandMoreIcon />
                        </ExpandMore>
                        <Button onClick={() => onPost && onPost(newPostPayload)}>
                            Post
                        </Button>
                    </CardActions>
                </Card>
            }
        </>

    );
}